body {
  margin: 0;
  padding: 0;
  font-family: 'Raleway', sans-serif;
}
/* custom buttons & text styles */
.wow{
  visibility: hidden;
}
.centered{
  text-align:center;
}
.h1-intro{
  font-size:2em;
  color: rgb(194, 2, 194);
}
.btn{
  border: 0;
}
.description{
  color: rgb(1, 78, 92);
  text-align: left;
  font-size:1.2em;
}
.custom-btn{
  display: show;
  opacity:1;
  background: -webkit-linear-gradient(right bottom, rgb(164, 74, 224), rgb(73, 70, 221));
  border: 0.05px solid transparent;
  border-radius: 25px;
  color: white;
}
.custom-btn:hover{
  color: white;
  -webkit-box-shadow: 4px 5px 1px rgb(44, 44, 44);
          box-shadow: 4px 5px 1px rgb(44, 44, 44);
}
#scroll-down{
  width:15%;
  margin: 0 auto;
  margin-top: 5%;
  opacity: 0.4;
}
#h1-head{
  opacity: 0;
  -webkit-transition:1s opacity ease;
  -o-transition:1s opacity ease;
  transition:1s opacity ease;
}
#marketing-line{
  text-align:left;
  color:grey;
  padding: 1% 10%;
  font-size: 1.1em;
  opacity:0;
  -webkit-transition: opacity 2s ease;
  -o-transition: opacity 2s ease;
  transition: opacity 2s ease;
}
.marketing-line{
  text-align:left;
  color:grey;
  padding: 1% 10%;
  font-size: 1.1em;
}
.border-bottom{
  border-bottom: 2px solid rgb(199, 3, 199) !important;
}
.emphasize{
  font-weight: 700;
  color: rgb(199, 3, 199);
}
.technology-icons img{
  width:100%;
}
.tool{
  color: rgb(6, 148, 214);
  text-transform: uppercase;
  letter-spacing: 1.2px;
  text-align:left;
}
.toolbox-container{
  padding: 3% 0;
  width:50%;
  margin:0 auto;
}
.custom-btn2{
  border: none;
  background: rgb(198, 247, 240);
  color:black;
  display:inline;
  margin: 10% 1%;
  margin-bottom: 3%;
  -webkit-transition: background 0.5s ease;
  -o-transition: background 0.5s ease;
  transition: background 0.5s ease;
}
.custom-btn2:hover{
  background: lightpink;
}
.project-nav{
  width:100%;
  text-align:right;
}
.project-image{
  width:100%;
}
.project{
  margin-top: 3%;
}
.overlay{
  background: rgb(1, 10, 29);
  color: lightblue;
  text-align:center;
  position:absolute;
  width:100%;
  top:0;
  left: 0;
  height:100%;
  padding: 5%;
}
/* navbar */
.navbar-toggler{
  width:20%;
  display:block;
  border: 0;
  text-align:left;
}
.navbar-toggler img{
  width:100%;
}
.nav-item a{
  text-transform: uppercase;
  letter-spacing: 1.3px;
}
#imac-img{
  width:100%;
  margin-top: -20px;
  margin-left: -60px;
}
#project-img{
  width:70%;
  margin-bottom: 5%;
}
#envelope-img{
  width:70%;
  margin: 5% 0;
}
.get-started-form{
  width:60%;
  margin: 0 auto;
  padding: 10% 5%;
  padding-bottom: 0;
}
.get-started-form input, textarea{
  width:100%;
  padding: 2% 3%;
  margin-top: 3%;
  border: 2px solid lightblue;
  border-radius: 20px;
  outline: none;
  color: lightpink;
  background: rgb(255, 250, 255);
}

/* sections */
#main{
  width:100%;
  background: url('/header.svg') no-repeat;
  background-size:100%;
}
#home img{
  width:60%;
}
#footer{
  background: url('/footer.png');
  background-size:cover;
  width:100% ;
  height:50vh;
}
.media-icons{
  text-align:left;
  padding-right: 45%;
  padding-top: 35vh;
  width: 70%;
}
.media-icons img:hover{
  position:absolute;
  top: -5px;
}
.copyright{
  margin-top: -15px;
  padding-right: 2%;
  width:100%;
  text-align:right;
  color: rgb(255, 255, 255);
  text-shadow:1px 1px 5px black;
  letter-spacing: 1.2px;
  text-transform:uppercase;
}
.tool-box{
  margin-top: 5%;
}
.help-container{
  padding-top:5%;
}

@media(min-width:701px) and (max-width:992px){
  .navbar{
      position: absolute;
      width: 100%;
      z-index: 9999;
      top:0;
  }
  #main{
    padding-top: 5%;
  }
  .navbar img{
    width:25%;
  }
  .description{
    display:none;
  }
  .media-show-line{
    display:show !important;
  }
  #what-i-do .col-sm-6 p{
    display:none;
  }
  .project-nav{
    margin-top: 10%;
  }
  .overlay p:not(.tool){
    display:none;
  }
  .overlay .tool{
    margin-top: 5%;
    text-align:center;
  }
  .get-started-form{
    width:80%;
    margin-top:10%;
  }
  #home .btn{
    margin-top: 5%;
  }
  .media-icons{
    padding-right: 30%;
  }
  #footer .container{
    width:100%;
    margin: 0;
  }
}
@media (max-width: 700px){
  h1{
    font-size:2em;
  }
  .media-show-line{
    display:none;
  }
  .navbar img{
    width:100%;
  }
  .media-icons{
      text-align:left;
      padding: 0;
      padding-top:45%;
      width: 100%;
  }
  .copyright{
    font-size: 1em;
    padding: 0 3%;
    text-align:center;
  }
  #h1-head{
    margin-top: 15%;
    color: purple;
    font-size: 2em;
    border-top: 2px solid purple;
  }
  .toolbox-container{
    width:100%;
    margin: 0;
    margin-top: 5%;
  }
  #scroll-down{
    display:none;
  }
  .project-nav{
    margin-top: 15%;
  }
  .custom-btn2{
    margin-top: 0;
  }
  .overlay p:not(.tool){
    display:none;
  }
  .overlay .tool{
    text-align:center;
  }
  .overlay .custom-btn{
    margin-top: 10%;
  }
  .get-started-form{
    width:100%;
  }
}
@media (min-width: 993px) and (max-width: 1050px){
  .h1-intro{
    font-size:1em;
    text-transform:uppercase;
    letter-spacing:1.3px;
  }
  .media-show-line{
    display:none;
  }
  .description{
    font-size: 1em;
  }
  .btn{
    font-size:0.7em;
  }
  #imac-img{
    width:90%;
  }
  .overlay p:not(.tool){
    font-size:0.9em;
  }
  .media-icons{
    padding-right: 40%;
  }
}
@media (min-width: 1050px) and (max-width:1439px){
  .media-show-line{
    display:none;
  }
}
@media(min-width: 1440px){
  .media-show-line{
    display:none;
  }
  #home img{
    width:90%;
    margin-left: -50%;
  }
  #imac-img, #project-img, #envelope-img{
    width:130%;
    margin-left: -50%;
  }
  .get-started-form{
    width:80%;
  }
}



